import React from 'react'
import ReactDOM from 'react-dom'
import ColumnResizer from "column-resizer";

const TableHeader = (props) => {

    const x = props.employeesData
    const keys = Object.keys(props.employeesData[0])
    const headers = keys.map(element => {
        return <th>{element}</th>
    })
    return (
        <thead><tr>{headers}</tr></thead>
    )
}

const TableBody = (props) => {

    const rows = props.employeesData.map((row, index) => {

        const keys = Object.keys(row)
        const rowData = keys.map(element => {
            return <td>{row[element]}</td>
        });

        return (
            <tr key={index}>{rowData}</tr>
        )
    })

    return <tbody>{rows}</tbody>
}

class Table extends React.Component {

    constructor(props) {
        super(props);
        this.tableId = this.props.tableId
        this.tableSelector = "#" + this.tableId;
        this.data = [{"id":"", "name":"", "username":"", "email":"", "address":"", "phone":"", "website":"", "company":""}]

        fetch('http://jsonplaceholder.typicode.com/users')
            .then(res => res.json())
            .then((data) => {
                this.data = []
                data.forEach(element => {
                    console.log(element)
                    element.address = element.address.city
                    element.company = element.company.name
                    this.data.push(element)
                });
            })
            .catch(console.log)
    }

    state = {
        contacts: []
    }

    componentDidMount() {
        if (this.props.resizable) {
            this.enableResize();
        }
    }

    componentWillUnmount() {
        if (this.props.resizable) {
            this.disableResize();
        }
    }

    componentDidUpdate() {
        if (this.props.resizable) {
            this.enableResize();
        }
    }

    componentWillUpdate() {
        if (this.props.resizable) {
            this.disableResize();
        }
    }

    enableResize() {
        const options = this.props.resizerOptions;
        if (!this.resizer) {
            this.resizer = new ColumnResizer(
                ReactDOM.findDOMNode(this).querySelector(`${this.tableSelector}`),
                options
            );
        }
        else {
            this.resizer.reset(options);
        }
    }

    disableResize() {
        if (this.resizer) {
            this.resizer.reset({ disable: true });
        }
    }

    getStudents() {
    };

    render() {

        const employeesData = this.data//this.props.employeesData

        return (
            <div>
                <table id={this.tableId} cellSpacing="0" className="table .table-bordered">
                    <TableHeader employeesData={employeesData} />
                    <TableBody employeesData={employeesData} />
                </table>
            </div>
        );
    }
}

export default Table